import styled from "styled-components";
import { motion, AnimatePresence } from "framer-motion";
import { useState } from "react";

const Paper = styled(motion.div)`
  position: relative;
  display: flex;
  padding: 50px;
  background-color: whitesmoke;
  border-radius: 30px;
  margin: 30;
  width: 90vw;
  height: 80vh;
`;

const Button = styled.button`
  text-align: center;
  position: absolute;
  font-size: 20px;
  width: 40px;
  height: 40px;
  bottom: 30px;
  right: 30px;
  background-color: "#fff";
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;
  &:hover {
    color: #dc143c;
    transform: scale(1.2);
  }
`;

const paperMotion = {
  start: { opacity: 0, bottom: -1000 },
  end: { opacity: 1, transition: { duration: 1 }, bottom: 0 },
  exit: { opacity: 0, bottom: -1000, transition: { durantion: 1 } },
};

const Letter = (props) => {
  const [visible, setVisible] = useState(true);
  return (
    <AnimatePresence>
      {visible && (
        <Paper
          variants={paperMotion}
          initial={"start"}
          animate={"end"}
          exit={"exit"}
          style={{
            display: "flex",
            alignContent: "center",
            justifyContent: "center",
            flexDirection: "column",
          }}
        >
          <h1
            style={{
              textAlign: "center",
              color: "#9999ff",
              marginBottom: "30px",
            }}
          >
            🎂 Happy birthday
          </h1>
          <h3 style={{ textAlign: "center", color: "#FF99CC" }}>
            Always be happy and healthy.
          </h3>
          <Button
            onClick={() => {
              setVisible(false);
              setTimeout(() => props.back(), 500);
            }}
          >
            ↩︎
          </Button>
        </Paper>
      )}
    </AnimatePresence>
  );
};

export default Letter;
